import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import MedialCards from './card';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function ScrollableTabsButtonAuto() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
        >
          <Tab label="LigthMaze" {...a11yProps(0)} />
          <Tab label="MES" {...a11yProps(1)} />
          <Tab label="Portfolio" {...a11yProps(2)} />
          <Tab label="Stage 1" {...a11yProps(3)} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <MedialCards hrf="https://maelnilba.github.io/LightMaze/" titre="LightMaze" lien="lightmaze" txt="Projet personnel ralis en JavaScript. Il s'agit d'un petit jeu avec un style rtro qui m'a permis d'appendre par moi mme en essayant l'approche Modle-vue-contrleur" tilte="LigthMaze Mini Jeu"/>
      </TabPanel>
      <TabPanel value={value} index={1}>
      <MedialCards hrf="https://maelnilba.github.io/MES/" titre="Mice Editor Studio" lien="mes" txt="Projet personnel ralis en JavaScript. Il est ici question d'un site permettant de dessiner  l'aide de simples lignes, avec la possibilite de changer les couleurs, grossir, se dplacer, zoomer, calques etc... Ce site est destin pour l'utilisation d'un jeu. Le projet est toujours en progrssion, mais actuellement en pause."/>
      </TabPanel>
      <TabPanel value={value} index={2}>
      <MedialCards hrf="maelrioux.ovh" titre="Portfolio" lien="portfolio" txt="Projet ralis avec React.JS. Il s'agit du portfolio ncessaire pour le BTS."/>
      </TabPanel>
      <TabPanel value={value} index={3}>
        Actuellement en recherche de stage.
      </TabPanel>
    </div>
  );
}


